/************
RESETS
************/

html {
    box-sizing: border-box;
    color: #444;
    font: 12pt 'Open Sans', Arial;
    font-weight: 400;
}

*, *::before, *::after {
    box-sizing: inherit;
    color: inherit;
    font: inherit;
}


.background-mogs-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    overflow: hidden;
    width: 100%;
    height: 100%;
}

.background-cover {
    position: absolute;
    background: rgba(252, 136, 29, 0.7);
    top: 0;
    bottom: 0;
    width: 100%;

}

.background-mogs-container .mog-img {
    margin: 10px;
    position: relative;
    /* display: inline-block; */
    background-size: cover;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: solid #FEBF44 2px;
    -webkit-box-shadow: 0px 10px 30px -8px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 10px 30px -8px rgba(0,0,0,0.75);
    box-shadow: 0px 10px 30px -8px rgba(0,0,0,0.75);
}

body {
	margin: 0;
	padding: 0;
	background-color: #EAEAEA;
}

body.login-body {
    margin: -25px 0 0 0;
    padding: 0;
    background-color: white;
    overflow: hidden;
}

navbar {
	width: 100%;
	/*position: absolute;*/
  	top: -16px;
}

navbar ul {
    display: flex;
    /* justify-content: space-between; */
    text-align: center;
    margin: 0;
    padding: 0;
    background-color: red;
}

navbar ul li {
	list-style: none;	
	flex: 1;
}

.nav-selected {
	border-bottom: solid yellow 5px;
}

a {
	text-decoration: none;
}

.navtab {
    flex: 1;
    height: 4em;
    padding-top: 1em;
    margin: 0;
}

