  -ms-flex-direction: row;
  flex-direction: row;
  padding: 0px 1em;
  width: 100%;
  height: 90%;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  align-content: flex-start;
  justify-content: flex-start; }

.game-field {
  margin: 3em auto;
  min-width: 480px;
  width: 85%;
  height: 600px;
  background-color: white;
  position: relative;
  overflow: hidden; }

.mog-drop-container {
  width: 100%;
  height: 100%;
  position: absolute;
  /* margin: auto; */ }

.slammer-explosion {
  position: relative;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%); }

.explosion-container {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%); }

.explosion {
  background: url("/images/memeslam.png");
  width: 300px;
  height: 300px;
  background-color: white;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center; }

.stack-itm-contr {
  position: absolute;
  -webkit-transform: rotate3d(1, 0, 0, 60deg); }

.mog-stack {
  position: relative;
  width: 150px;
  height: 600px;
  margin: auto; }

.drop-item {
  position: absolute;
  width: 100px;
  height: 100px;
  border: 2px solid orange;
  border-radius: 50%;
  background-size: cover;
  background-color: white;
  background-position: center;
  /* -webkit-transform: rotate3d(1, 0, 0, 60deg); */
  box-shadow: 5px 5px 30px -9px rgba(102, 102, 102, 0.5); }

.stack-item:first-child {
  -webkit-box-shadow: 15px 10px 20px 1px #adadad;
  -moz-box-shadow: 15px 10px 20px 1px #adadad;
  box-shadow: 15px 10px 20px 1px #adadad; }

.stack-item:last-child {
  background: url("/images/memeslam.png");
  background-color: white;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center; }

.stack-item {
  position: absolute;
  width: 150px;
  height: 150px;
  border: 2px solid orange;
  border-radius: 50%;
  -webkit-transform: rotate3d(1, 0, 0, 60deg);
  box-shadow: 0px 1px 0px 1px #e8e8e8; }

.display-stack h3 {
  position: absolute;
  text-align: center;
  left: 50%;
  transform: translateX(-50%);
  top: 50px;
  font-size: 3em; }

.game-details h3 {
  padding-left: 3em; }

.user-mogs, .won-mogs {
  margin: auto;
  width: 85%;
  display: flex;
  flex-wrap: wrap;
  justify-content: left;
  border-bottom: 1px solid black; }

/****
SLAMMER CSS
***/
.slammer-container {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%); }

.slammer-container > h3 {
  font-size: 3em; }

.slammer {
  width: 400px;
  position: relative; }

.upper {
  position: absolute;
  top: -170px;
  height: 200px;
  width: 400px;
  background-color: orange;
  border-radius: 400px 400px 0 0;
  -moz-border-radius: 400px 400px 0 0;
  -webkit-border-radius: 400px 400px 0 0; }

.upper:hover, .lower:hover {
  background-color: yellow; }

.lower {
  height: 200px;
  position: absolute;
  width: 400px;
  background-color: blue;
  border-radius: 0 0 400px 400px;
  -moz-border-radius: 0 0 400px 400px;
  -webkit-border-radius: 0 0 400px 400px;
  top: 30px; }

.gates {
  height: 60px;
  width: 400px;
  background-color: white;
  position: relative; }

.enter {
  float: left;
  position: absolute;
  height: 60px;
  width: 375px;
  background-color: green;
  z-index: 1; }

.exit {
  float: right;
  position: absolute;
  height: 60px;
  width: 25px;
  background-color: red;
  right: 0px;
  z-index: 1; }

::-webkit-input-placeholder {
  padding-left: 5px; }

